const transform = require('./util/transform')
const picker = require('../picker')

const add = (p1, p2) => ({ x: p1.x + p2.x, y: p1.y + p2.y })

const snek = (gen, name, palette) => {
  const shapeGen = gen.withNames({ shape: 'snek', name })

  const snekBase = shapeGen.repeat('snek-size', (index) => {
    const pos = shapeGen.point({ 'snek-point': index }, -1, 1)
    const speed = shapeGen.point({ 'snek-speed': index }, -0.5, 0.5)

    return { pos, speed }
  }, 2, 4)

  const sneks = shapeGen.repeat('sneks', (index) => {
    return {
      points: snekBase.map((snekPoint, snekIndex) => ({
        pos: add(snekPoint.pos, shapeGen.point({ 'snek': index, 'point': snekIndex }, -0.1, 0.1)),
        speed: add(snekPoint.speed, shapeGen.point({ 'snek': index, 'speed': snekIndex }, -0.1, 0.1))
      })),
      color: picker(shapeGen, { 'snek-color': index }, palette)
    }
  }, 2, 5)

  const snekWidth = shapeGen.float('snek-width', 0.05, 0.1)

  const render = ({ context, origin, scale, palette }) => {
    transform(context, origin, scale)

    context.lineWidth = snekWidth
    context.lineCap = 'round'
    context.lineJoin = 'round'

    sneks.map(snek => {
      context.beginPath()
      context.moveTo(snek.points[0].x, snek.points[0].y)

      const next = (index) => snek.points[index + 1]
      snek.points.slice(0, -1).map((point, index) => {
        const nextPoint = next(index)

        context.bezierCurveTo(
          point.pos.x + point.speed.x,
          point.pos.y + point.speed.y,
          nextPoint.pos.x - nextPoint.speed.x,
          nextPoint.pos.y - nextPoint.speed.y,
          nextPoint.pos.x,
          nextPoint.pos.y
        )
      })

      context.strokeStyle = snek.color(context)
      context.stroke()
    })

    context.restore()
  }

  return { render, name: 'snek' }
}

module.exports = snek
